body {
  height: 100vh;
}

.wrapper {
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

[data-stop], [data-start] {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  outline-offset: 0px;
  text-shadow: none;
  background: #337ab7;
  border: 2px solid #bfe2ff;
  outline: 1px solid #337ab7;
  margin: 10px;
  padding: 12px 30px;
  font-family: Roboto Condensed, Тahoma, sans-serif;
  font-size: 20px;
  font-weight: bold;
  line-height: 20px;
  text-decoration: none;
  transition: all 1.5s cubic-bezier(.19, 1, .22, 1);
  display: inline-flex;
  position: relative;
}

[data-stop]:enabled:hover, [data-stop]:enabled:focus, [data-start]:enabled:hover, [data-start]:enabled:focus {
  outline-offset: 15px;
  color: #fff;
  outline-color: rgba(255, 255, 255, 0);
  box-shadow: inset 0 0 20px #bfe2ff;
}

[data-stop]:disabled, [data-start]:disabled {
  background-color: #bfe2ff;
}

